import { Request } from "@/utils/request";

//  获取知识审核列表
export const getWx = (params) => {
  return Request({
    url: "/cp/js-api-signature",
    params,
    method: "get",
  });
};

export const getWxConfig = (params) => {
  return Request({
    url: "/cp/agent-js-api-signature",
    params,
    method: "get",
  });
};

export const getOrders = (params) => {
  return Request({
    url: "/cp/orders",
    params,
  });
};

export const getUserProfile = (params) => {
  return Request({
    url: "/cp/user-profile",
    params,
  });
};
